import React, { PureComponent } from 'react';
import Select from 'react-select';
import { connect } from 'react-redux';

import { setLocalization } from '../../actions/actions';
import { setLocalizationPT, currentLanguagePT } from '../../helpers/proptypes.helper';
import strings, { CODE_ENG, CODE_RUS, CODE_UA, defaultLanguageCode } from './localization';

import LanguageSelectorContainer, {
  CountryFlag,
  LanguageOptionContainer,
  LanguageOptionLabel,
} from './LanguageSelector.styled';

/**
 * Emoji flags not yet implemented in browsers, so base64 images were used
 */
const languageOptions = [
  { value: CODE_ENG, label: 'English', imgBase64: 'data:image/png;base64,' },
  { value: CODE_RUS, label: '', imgBase64: 'data:image/png;base64,' },
  { value: CODE_UA, label: '', imgBase64: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAEgAAABIBAMAAACnw650AAAAG1BMVEVHcEwAW7v/1QAAW7v/1QAAW7v/1QD/1QAAW7uIqOaMAAAAB3RSTlMAx8dgYCAg36fI1gAAAGpJREFUSMdjYBgFdAeGHXiBMEhNYAcBIMrAwNpBEAQwMBJWJMBgQVhRM4MEYUWNDB1EgFFFo4qGsqJyIsCoolFFQ1mROmE1RQzuhBWVMDARVqTAwEZYUQIDQxIhNWqgqtMJvxqV0QYI/QEAHf+XxuFYqnUAAAAASUVORK5CYII=' },
];

class LanguageSelector extends PureComponent {
  static propTypes = {
    currentLanguage: currentLanguagePT.isRequired,
    setLocalization: setLocalizationPT.isRequired,
  };

  onLanguageSelect = (language) => {
    if (!language) { return; }

    const selectedLanguageCode = language.value;
    const currentLanguageCode = selectedLanguageCode || defaultLanguageCode;
    strings.setLanguage(currentLanguageCode);
    this.props.setLocalization(strings);
  };

  renderOption = option => (
    <LanguageOptionContainer>
      <CountryFlag src={option.imgBase64} alt={option.value} />
      <LanguageOptionLabel>{option.label}</LanguageOptionLabel>
    </LanguageOptionContainer>
  );

  render() {
    return (
      <LanguageSelectorContainer>
        <Select
          name="language"
          id="select-language"
          value={this.props.currentLanguage}
          onChange={this.onLanguageSelect}
          options={languageOptions}
          clearable={false}
          searchable={false}
          optionRenderer={this.renderOption}
        />
      </LanguageSelectorContainer>
    );
  }
}

function mapStateToProps(state) {
  return {
    currentLanguage: state.appCommonParams.currentLanguage,
  };
}

export default connect(mapStateToProps, {
  setLocalization,
})(LanguageSelector);



